<template>
  <div>
    <van-nav-bar
      :title="title"
      left-text=""
      left-arrow
      fixed
      @click-left="onClickLeft"
    />

    <div class="pdf_box"></div>

  </div>
</template>

<script>
  export default {
    name: "financialTimesDetail",
    data() {
      return {
        title: '',
        fileId: '',
        url: ''
      };
    },
    methods: {
      onClickLeft() {
        this.$router.back();
      },
      async renderPageAsync(pdf, numPages, current) {
        console.log("renderPage async");
        for (let i = 1; i <= numPages; i++) {
          // page
          let page = await pdf.getPage(i);

          let scale = .6;
          let viewport = page.getViewport(scale);
          // Prepare canvas using PDF page dimensions.
          let canvas = document.createElement("canvas");
          let context = canvas.getContext('2d');
          document.getElementsByClassName('pdf_box')[0].appendChild(canvas);

          canvas.height = viewport.height;
          canvas.width = viewport.width;

          // Render PDF page into canvas context.
          let renderContext = {
            canvasContext: context,
            viewport: viewport
          };
          page.render(renderContext);
        }
      }
    },
    mounted() {
      this.title = this.$route.params.title;
      this.fileId = this.$route.params.id;
      this.url = cfaeIp + '/mall/jgjrrb/downLoadOrOpen.json?fileId=' + this.fileId + '&contentType=open'
      pdfjsLib.getDocument(this.url).then(pdf=>{
        var numPages = pdf.numPages;
        var start = 1;
        this.renderPageAsync(pdf, numPages, start);
      });
    }

  }
</script>

<style scoped>
  .pdf_box {
   margin-top: 46px;
   background:#7F7F7F;
   display: flex;
   justify-content: center;
   flex-wrap: wrap;
  }

</style>



